import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

@Injectable()
export class CheckoutService {
  paypalUrl = '/api/paypal/checkout';
  constructor(private http: Http) { }

  paypal() {
    return this.http.get(this.paypalUrl)
      .map(res => res.json());
  }
}



