import { Injectable } from '@angular/core';
import { CanActivate } from '@angular/router/src/interfaces';
import { Router } from '@angular/router';
import { ToastService } from '../../typescripts/pro';

@Injectable()
export class AuthGuardService implements CanActivate{

  constructor(private router: Router, private toastrService: ToastService) { }

  canActivate(): boolean{
    if( localStorage.getItem('currentUser') == null){
      let options = { closeButton: true, tapToDismiss: true, positionClass: 'toast-bottom-right' };
     this.toastrService.error('Please Login', 'Unauthorized access!', options);

      this.router.navigate(['']);
      return false;
    }
    return true;
  }

}



