import { Component, OnInit, ViewChild } from '@angular/core';
import { ModalDirective } from 'app/typescripts/free';
import { AuthenticationService } from 'app/services/authentication.service';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss']
})
export class SignUpComponent implements OnInit {
  @ViewChild('autoShownModal')
  firstName: string;
  lastName: string;
  emailId: string;
  new_password: string;
  confirmPassword: string;
  signupError: string;

  constructor(public autoShownModal: ModalDirective, private authenticationService: AuthenticationService) {

  }

  ngOnInit() {
  }

  onSubmitSignUP() {
    const newUser = {
      first_name: this.firstName,
      last_name: this.lastName,
      emailId: this.emailId,
      password1: this.new_password,
      password2: this.confirmPassword
    }

    if (newUser.first_name === undefined) {
      this.signupError = 'Enter First Name';
      return false;
    } else if (newUser.last_name === undefined) {
      this.signupError = 'Enter Last Name';
      return false;
    } else if (newUser.emailId === undefined) {
      this.signupError = 'Enter Email Id';
      return false;
    }

    if (newUser.password1 === undefined) {
      this.signupError = "Enter Password";
      return false;
    } else if (newUser.password2 === undefined) {
      this.signupError = "Enter Confirm Password";
      return false;
    }

    if (newUser.password1 != newUser.password2) {
      this.signupError = "Password not matched";
      return false;
    }

    this.authenticationService.signUp(newUser)
      .subscribe(data => {
        if (data.status === 0) {
          this.signupError = 'Email already exists !';
        } else if (data.status === 1) {
          let user = {
            username: this.emailId,
            password: this.new_password,
          }
          this.authenticationService.login(user).subscribe(data => {
            this.authenticationService.storeUserData(data.access_token, user.username);
          });
          this.autoShownModal.hide();
        }
      });
  }
}



