import { Component, OnInit } from '@angular/core';
import { ProfileService } from '../profile.service';
import { ToastService } from '../../typescripts/pro';

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.scss']
})
export class EditProfileComponent implements OnInit {
  youtubeLink: String;
  linkedinLink: String;
  facebookLink: String;
  twitterLink: String;
  gplusLink: String;
  websiteLink: String;
  biography: String;
  language;
  profile = {};
  optionsSelect = [];
  profileData;
  firstName: String;
  lastName: String;
  headLine: String;
  constructor(private profileService: ProfileService, private toast: ToastService) { }

  ngOnInit() {
    this.profileService.getProfile()
      .subscribe(profile_data => {
        this.profileData = profile_data;
        this.optionsSelect = this.profileData.languages;
      });
    this.getProfile();
  }

  getProfile() {
    this.profileService.getProfile()
      .subscribe(profile_data => {
        this.profileData = profile_data;
        if (this.profileData.status != 0) {
          this.firstName = this.profileData.user.firstName;
          this.lastName = this.profileData.user.lastName;
          this.headLine = this.profileData.user.headline;
          this.biography = this.profileData.user.biography;
          this.language = "" + this.profileData.user.language;
          this.websiteLink = this.profileData.user.websiteLink;
          this.gplusLink = this.profileData.user.gplusLink;
          this.twitterLink = this.profileData.user.twitterLink;
          this.facebookLink = this.profileData.user.facebookLink;
          this.linkedinLink = this.profileData.user.linkedinLink;
          this.youtubeLink = this.profileData.user.youtubeLink;
        }
      });

  }

  saveProfile() {
    this.profile = {
      firstName: this.firstName, lastName: this.lastName, headline: this.headLine, biography: this.biography,
      language: this.language, websiteLink: this.websiteLink, gplusLink: this.gplusLink, twitterLink: this.twitterLink,
      facebookLink: this.facebookLink, linkedinLink: this.linkedinLink, youtubeLink: this.youtubeLink
    }

    this.profileService.storeProfileData(this.profile)
      .subscribe(data => {
        let options = { closeButton: true, tapToDismiss: true, positionClass: 'toast-top-right' };
        if (data.status == 1) {
          this.toast.success('', 'Your changes have been successfully saved.!', options);
          this.getProfile();
        } else {
          this.toast.error('', 'Oops! Someting went wrong.!', options);
          return false;
        }
      });
  }

}



