import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { tokenNotExpired, JwtHelper } from 'angular2-jwt';
import 'rxjs/add/operator/map';

@Injectable()
export class AuthenticationService {
  authToken;
  currentUser: string;
  loginUrl: string = '/api/frontend/login';
  signupUrl: string = '/api/frontend/signup/';
  forgotUrl: string = '/api/password_reset/';

  jwtHelper: JwtHelper = new JwtHelper();
  
  private headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' });
  constructor(private http: Http) { }

  // Function to login user
  login(user) {
    let body = `username=${user.username}&password=${user.password}`;
    return this.http.post(this.loginUrl, body, { headers: this.headers })
      .map(res => res.json());
  }

  //Function to logout from current session
  logout() {
    this.authToken = null;
    this.currentUser = null;
    localStorage.removeItem('currentUser');
    localStorage.removeItem('user_id');
    localStorage.removeItem('token');
  }


  // Sign UP
  signUp(newUser) {
    let body = `first_name=${newUser.first_name}&last_name=${newUser.last_name}&email=${newUser.emailId}&password1=${newUser.password1}&password2=${newUser.password2}`;
    return this.http.post(this.signupUrl, body, { headers: this.headers })
      .map(res => res.json());
  }


  // Function to store user data with token in local storage
  storeUserData(token, username) {
    var userId = this.jwtHelper.decodeToken(token);
    localStorage.setItem('token', token);
    localStorage.setItem('currentUser', JSON.stringify(username));
    localStorage.setItem('user_id', userId.user_id);
    this.authToken = token;
    this.currentUser = username;
     
  }

  //Forgot Password

  forgotPassword(userEmailId) {
    let body = `email=${userEmailId.email}`;
    return this.http.post(this.forgotUrl, body, { headers: this.headers })
      .map(res => res.json());
  }

  loggedIn() {
    return tokenNotExpired();
  }
}



