import { Component, OnInit } from '@angular/core';
import { NavbarService } from 'app/shared/navbarService/navbar.service';
import { Router, ActivatedRoute } from '@angular/router';
import { MycoursesService } from '../mycourses.service';

@Component({
  selector: 'app-lectures',
  templateUrl: './lectures.component.html',
  styleUrls: ['./lectures.component.scss']
})
export class LecturesComponent implements OnInit {
  course_id: any;
  id: number;
  video_Id: string;
  url: any;
  marginLeft: number;
  width: number;
  transition: string;
  sideNavOpened: boolean = false;
  itemsList;

  constructor(public nav: NavbarService, private router: Router, private route: ActivatedRoute, private myCourse: MycoursesService) { }

  ngOnInit() {
    this.nav.hide();
    this.route.params.subscribe(params => {
     this.course_id = params.id;
      this.video_Id = params['video_id'];
      this.video_Id = `https://player.vimeo.com/video/${this.video_Id}`;
    });

    this.myCourse.getMyCourseVideos(this.course_id)
      .subscribe(data => {
        this.itemsList = data.curriculum;
      });
  }

  openNav() {
    this.sideNavOpened = true;
    document.getElementById("mySidenav").style.width = "400px";
    document.getElementById("main").style.marginLeft = "400px";
    document.body.style.backgroundColor = "rgba(0,0,0,0.4)";
  }

  closeNav() {
    this.sideNavOpened = false;
    document.getElementById("mySidenav").style.width = "0";
    document.getElementById("main").style.marginLeft = "0";
    document.body.style.backgroundColor = "white";
  }

  getUrl(a) {
    this.url = a.preview_url;
    this.closeNav();
    this.router.navigate(['/lectures', {id:this.course_id, video_id : this.url }]);
  }

  gotoDashboard() {
    this.router.navigate(['/mycourses']);
  }
}



